header {
    height: 110vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}

/* CTA */
.cta {
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

/* Social Icons */
.header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.header__socials::after {
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

/* Image */

.me {
    background: linear-gradient(var(--color-primary-2), transparent);
    width: 22rem;
    height: 22rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 3rem 1.5rem 1.5rem 1.5rem;
}

/* Scroll down */

.scroll__down {
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

h1 {
    /* color: var(--color-primary-2); */
    font-size: 4rem;
    background-image: linear-gradient(to left, var(--color-primary), #4A9980);
    color: transparent;
    background-clip: text;
    -webkit-background-clip: text;
    font-weight: 600;
}

/* Media queries- medium devices */

@media screen and (max-width: 1024px) {
    .header {
        height: 68vh;
    }
}

/* Media queries- small devices */
@media screen and (max-width: 600px) {
    .header {
        height: 100vh;
    }

    h1 {
        font-size: 3rem;
    }

    .header__socials, 
    .scroll__down {
        display: none;
    }
}